import React, { useContext } from "react";
import { AuthContext } from "../context/AuthContext";
import { useNavigate } from "react-router-dom";
import "./LoginPage.css"; // Reusing styles from LoginPage
import "./AboutHome.css";

export default function About() {
  const { language, toggleLanguage } = useContext(AuthContext);
  const navigate = useNavigate();

  return (
    <div className={language === "ar" ? "rtl" : "ltr"}>
      {/* Header Section outside the scrollable container */}
      <header className={`login-header ${language === "ar" ? "rtl" : "ltr"}`}>
        <h1 className="login-title1">
          {language === "en" ? "Abber" : ""}
        </h1>

        {/* Language Switch Button */}
        <button className="login-language-switch" onClick={toggleLanguage}>
          {language === "en" ? "" : "English"}
        </button>
      </header>

      {/* About Section */}
      <div className="about-container">
        <div className="about-content" style={{ textAlign: language === "ar" ? "right" : "left", direction: language === "ar" ? "rtl" : "ltr" }}>
          <h1 className="about-title">
            {language === "en" ? "About Us" : " "}
          </h1>

          <section className="about-section">
            <h2>{language === "en" ? "Our Mission" : ""}</h2>
            <p>
              {language === "en"
                ? "We aim to support children with autism in understanding and recognizing emotions through an AI-powered Facial Emotion Detection System. Our goal is to improve their communication and social interaction skills by providing real-time feedback on facial expressions."
                : "                 .                ."}
            </p>
          </section>

          <section className="about-section">
            <h2>{language === "en" ? "What We Do" : "  "}</h2>
            <p>
              {language === "en"
                ? "Our web-based system uses AI, machine learning, and computer vision to analyze facial emotions and provide instant feedback in an interactive and user-friendly way."
                : "                    ."}
            </p>
          </section>

          <section className="about-section">
            <h2>{language === "en" ? "Why It Matters" : "  "}</h2>
            <ul style={{ textAlign: language === "ar" ? "right" : "left", direction: language === "ar" ? "rtl" : "ltr" }}>
              <li>{language === "en" ? "Real-time emotion analysis to help children understand facial expressions." : "          ."}</li>
              <li>{language === "en" ? "A friendly and interactive interface designed for ease of use." : "   ."}</li>
              <li>{language === "en" ? "Personalized features that adapt to different users needs." : "      ."}</li>
            </ul>
          </section>

          <section className="about-section">
            <h2>{language === "en" ? "How It Works" : " "}</h2>
            <ol style={{ textAlign: language === "ar" ? "right" : "left", direction: language === "ar" ? "rtl" : "ltr" }}>
              <li>{language === "en" ? "The child or parent opens the web app." : "    ."}</li>
              <li>{language === "en" ? "The camera detects and analyzes facial expressions using AI." : "       ."}</li>
              <li>{language === "en" ? "The system displays real-time feedback with text and emojis." : "       ."}</li>
              <li>{language === "en" ? "The child learns to associate emotions with expressions, improving social understanding." : "        ."}</li>
            </ol>
          </section>

          <section className="about-section">
            <h2>{language === "en" ? "Join Us in Making a Difference!" : "    !"}</h2>
            <p>
              {language === "en"
                ? "We are dedicated to using technology to support children with autism in their emotional and social development. Whether youre a parent, educator, or researcher, our platform offers an innovative way to foster emotional learning."
                : "           .              ."}
            </p>
          </section>
        </div>

        {/* Back to Home Button */}
        <button className="home-button primary" onClick={() => navigate("/")}>
          {language === "en" ? "Back to Home" : "  "}
        </button>
      </div>
    </div>
  );
}
