import React, { useContext } from "react";
import { AuthContext } from "../context/AuthContext";
import { useNavigate } from "react-router-dom"; // To navigate between pages
import "./LoginPage.css"; // Reusing styles from LoginPage
import "./Help.css"; // Adding Help-specific styles

export default function Help() {
  const { language, toggleLanguage } = useContext(AuthContext); // Access language context
  const navigate = useNavigate(); // To navigate between pages

  return (
    <div className={language === "ar" ? "rtl" : "ltr"}>
      {/* Header Section */}
      <header className={`login-header ${language === "ar" ? "rtl" : "ltr"}`}>
        <h1 className="login-title1">
          {language === "en" ? "Abber" : ""}
        </h1>

        {/* Language Switch Button */}
        <button className="login-language-switch" onClick={toggleLanguage}>
          {language === "en" ? "" : "English"}
        </button>
      </header>

      {/* Help Section */}
      <div className="help-container">
        <div
          className="help-content"
          style={{
            textAlign: language === "ar" ? "right" : "left", // Align text based on language
            direction: language === "ar" ? "rtl" : "ltr", // Set text direction based on language
          }}
        >
          <h1 className="help-title">
            {language === "en" ? "Help & Support" : " "}
          </h1>

          <section className="help-section">
            <h2>{language === "en" ? "1. Introduction" : "1. "}</h2>
            <p>
              {language === "en"
                ? "This website provides facial emotion detection to help children with autism recognize emotions and improve social interactions. It works in real-time and requires a camera."
                : "                .      ."}
            </p>
            <p>
              {language === "en"
                ? "It is designed for children, parents, and educators."
                : "    ."}
            </p>
          </section>

          <section className="help-section">
            <h2>{language === "en" ? "2. How to Use the Website" : "2.   "}</h2>

            <h3>{language === "en" ? "A. Creating an Account" : ".  "}</h3>
            <ol>
              <li>{language === "en" ? "Click on 'Get Started' on the homepage." : "  ''   ."}</li>
              <li>{language === "en" ? "Enter your username and password." : "    ."}</li>
              <li>{language === "en" ? "Choose an avatar that represents you." : "   ."}</li>
              <li>{language === "en" ? "Click 'Continue' to complete the setup." : "  ''  ."}</li>
              <li>{language === "en" ? "If you already have an account, click 'Login' instead." : "       ' '   ."}</li>
            </ol>

            <h3>{language === "en" ? "B. Navigating the Website" : ".   "}</h3>
            <ul>
              <li>{language === "en" ? "Home Page  Introduction to the website." : "     ."}</li>
              <li>{language === "en" ? "Emotion Detection Page  The main tool to analyze emotions." : "       ."}</li>
              <li>{language === "en" ? "Profile Page  Manage your account details and avatar." : "        ."}</li>
              <li>{language === "en" ? "Help Page  Instructions on how to use the website." : "       ."}</li>
              <li>{language === "en" ? "About Page  Information about the system and our mission." : "      ."}</li>
            </ul>
          </section>

          <section className="help-section">
            <h2>{language === "en" ? "3. Understanding the Results" : "3.  "}</h2>
            <ul>
              <li>{language === "en" ? "Happy   The person is feeling positive." : "     ."}</li>
              <li>{language === "en" ? "Sad   The person may need comfort." : "       ."}</li>
              <li>{language === "en" ? "Angry   The person is upset; be calm." : "      ."}</li>
              <li>{language === "en" ? "Neutral   No strong emotions detected." : "       ."}</li>
              <li>{language === "en" ? "Surprised   Something unexpected happened." : "      ."}</li>
              <li>{language === "en" ? "Fear   The person may be scared." : "      ."}</li>
              <li>{language === "en" ? "Disgust   The person dislikes something." : "      ."}</li>
            </ul>
          </section>

          <section className="help-section">
            <h2>{language === "en" ? "4. Troubleshooting & Common Issues" : "4.     "}</h2>

            <h3>{language === "en" ? "A. Camera Not Working?" : ".   "}</h3>
            <ul>
              <li>{language === "en" ? "Ensure you have allowed camera access." : "       ."}</li>
              <li>{language === "en" ? "Refresh the page or restart your browser." : "      ."}</li>
              <li>{language === "en" ? "Try a different browser (Google Chrome, Edge, etc.)." : "   (   )."}</li>
            </ul>

            {/* New Section: B. Ensure Good Internet Connection */}
            <h3>{language === "en" ? "B. Ensure Good Internet Connection" : ".     "}</h3>
            <ul>
              <li>{language === "en" ? "A stable internet connection is necessary for accurate emotion detection." : "       ."}</li>
              <li>{language === "en" ? "Slow internet may affect the speed of the system's response and the accuracy of results." : "         ."}</li>
            </ul>
          </section>

          {/* Back to Home Button */}
          <button className="help-button primary" onClick={() => navigate("/main-page")}>
            {language === "en" ? "Back to Home" : "  "}
          </button>
        </div>
      </div>
    </div>
  );
}
